import { getSearchSongData } from '../../../Request/search'
import * as actionTypes from './actionType'

// 改变歌曲列表Action
const changeSongListAction = (songs: any) => ({
  type: actionTypes.CHANGE_SEARCH_SONG_LIST,
  songs
})

// 改变歌手列表
const changeSingerListAction = (singerList: any) => ({
  type: actionTypes.CHANGE_SINGER_LIST,
  singerList: singerList
})

// 搜索歌曲列表network
export const getSearchSongListAction = (songName: any, limit: any, type = 1) => {
  return (dispatch: (arg0: { type: string; songs: any }) => void) => {
    getSearchSongData(songName, limit, type).then((res: any) => {
      const songs = res && res.result.songs
      dispatch(changeSongListAction(songs))
    })
  } 
}

// 搜索歌手列表network
export const getSearchSingerListAction = (song: any, limit: any, type: number | undefined) => {
  return (dispatch: (arg0: { type: string; singerList: any }) => void) => {
    getSearchSongData(song, 20, type).then((res: any) => {
      const singerList = res.result && res.result.artists
      console.log(singerList)
      dispatch(changeSingerListAction(singerList))
    })
  }
}