import request from './request'

export function getTopBanners() {
    return request({
        url: "/banner"
    })
}

export function getHotRecommends(limit: any) {
    return request({
        url: "/personalized",
        params: {
            limit
        }
    })
}

export function getNewAlbums(limit: any, offset: any) {
    return request({
        url: "/top/album",
        params: {
            limit,
            offset
        }
    })
}

export function getTopList(idx: any) {
    return request({
        url: "/top/list",
        params: {
            idx
        }
    })
}

export function getArtistList(limit: any, cat: any) {
    return request({
        url: "/artist/list",
        params: {
            cat,
            limit
        }
    })
}