import React, { useState } from 'react'
import propTypes from 'prop-types'
import { getParseLoginState, getMatchReg } from '../../Utils/formatUtils'
import { Form, Input, Button, Checkbox, message } from 'antd'
import './loginForm.css'
import { useDispatch } from 'react-redux'
import { getLoginProfileInfo } from '../login/store/actionCreator'
import { sendRegisterCode, sendRegister } from '../../Request/login'

const layout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 16 },
}
const tailLayout = {
  wrapperCol: { span: 30 },
}

const LoginForm = (props: any) => {
  const { loginState } = props
  const [phone, setPhone] = useState('')
  const [isSendSatte, setIsSendSatte] = useState(false)
  const [second, setSecond] = useState(60)
  const parseLoginModeText: any = getParseLoginState(loginState)
  const mathchReg: any = getMatchReg(loginState)
  const mathchPhoneReg: any = getMatchReg('phone')
  const pwdReg: any = /[0-9a-zA-Z._-]{6,20}/
  const codeReg: any = /[0-9a-zA-Z._-]{4,20}/
  const dispatch = useDispatch()

  const onFinish = ({ username, password }: any) => {
    dispatch(getLoginProfileInfo(username, password, true))
  }

  const onFinishFailed = (errorInfo: any) => {
    console.log('Failed:', errorInfo)
  }

  // 注册
  const onRegisterFinish = (value: any) => {
    const { phone, password, code, nickname } = value
    sendRegister(code, phone, password, nickname).then((res: any) => {
      console.log(res)
      if (res.code === 200) message.success('注册成功')
      else message.warn(res.message)
    })
  }
  const onRegisterFinishFailed = () => {}

  // handle function
  const handleSendCode = () => {
    // 60秒延迟定时器
    if (!isSendSatte) {
      let i = 0
      const timer = setInterval(() => {
        i++
        setSecond(second - i)
        if (i >= 60) {
          clearInterval(timer)
          setIsSendSatte(false)
          setSecond(60)
        }
      }, 1000)
      !isSendSatte &&
        sendRegisterCode(phone).then((res: any) => {
          if (res.code === 200) message.success('发送成功')
          else message.warn('发送失败, 请60秒后发送验证码')
        })
    }
    setIsSendSatte(true)
  }

  return (
    <>
      <Form
        style={{
          display: loginState !== 'register' ? 'block' : 'none',
        }}
        {...layout}
        name="basic"
        initialValues={{ remember: true }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
      >
        <Form.Item
          label={parseLoginModeText}
          name="username"
          rules={[
            {
              pattern: mathchReg,
              message: `请输入正确的${parseLoginModeText}`,
            },
            { required: true, message: '请输入你的账户' },
          ]}
        >
          <Input autoFocus />
        </Form.Item>

        <Form.Item
          label="密码"
          name="password"
          rules={[
            { pattern: pwdReg, message: '密码最短6位' },
            { required: true, message: '请输入你的密码!' },
          ]}
        >
          <Input.Password />
        </Form.Item>
        <div className="textAlignRight">
          <Checkbox className="mr80" defaultChecked={true}>
            自动登录
          </Checkbox>
          <span className="forgetPwd">忘记密码?</span>
        </div>
        <Form.Item {...tailLayout}>
          <Button
            type="primary"
            htmlType="submit"
            size="middle"
            block
            shape="round"
          >
            登录
          </Button>
        </Form.Item>
      </Form>
      <Form
        style={{
          display: loginState === 'register' ? 'block' : 'none',
        }}
        {...layout}
        name="basic"
        onFinish={onRegisterFinish}
        onFinishFailed={onRegisterFinishFailed}
      >
        <Form.Item
          label={parseLoginModeText}
          name="phone"
          rules={[
            {
              pattern: mathchPhoneReg,
              message: `请输入正确的手机号`,
            },
            { required: true, message: '请输入你的手机号' },
          ]}
        >
          <Input
            autoFocus
            value={phone}
            onChange={({ target }) => {
              setPhone(target.value)
            }}
          />
        </Form.Item>

        <Form.Item
          label="密码"
          name="password"
          rules={[{ pattern: pwdReg, message: '密码最短6位', required: true }]}
        >
          <Input.Password />
        </Form.Item>
        <Form.Item>
          <div
            className="register"
            onClick={() => handleSendCode()}
          >
            {isSendSatte ? second + 's' : '发送验证码'}
          </div>
        </Form.Item>
        <Form.Item
          className="formGap"
          label="验证码"
          name="code"
          rules={[{ pattern: codeReg, message: '验证码最短4位' }, { required: true, message: '请输入你的验证码' }]}
        >
          <Input disabled={!isSendSatte} />
        </Form.Item>
        <Form.Item
          className="formGap"
          label="昵称"
          name="nickname"
          rules={[{ required: true, message: '请输入你的昵称' }]}
        >
          <Input disabled={!isSendSatte} />
        </Form.Item>
        <Form.Item {...tailLayout}>
          <Button
            type="primary"
            htmlType="submit"
            size="middle"
            block
            shape="round"
          >
            提交
          </Button>
        </Form.Item>
      </Form>
    </>
  )
}

LoginForm.propTypes = {
  loginState: propTypes.string,
}

LoginForm.defaultProps = {
  loginState: 'phone',
}

export default LoginForm
